import { Dialog } from '@jupyterlab/apputils';
const notImplemented = [];
export class DialogEnter extends Dialog {
    /**
     * Create a dialog panel instance.
     *
     * @param options - The dialog setup options.
     */
    constructor(options = {}) {
        super(options);
    }
    handleEvent(event) {
        switch (event.type) {
            case 'keydown':
                this._evtKeydown(event);
                break;
            case 'click':
                this._evtClick(event);
                break;
            case 'focus':
                this._evtFocus(event);
                break;
            case 'contextmenu':
                event.preventDefault();
                event.stopPropagation();
                break;
            default:
                break;
        }
    }
    _evtKeydown(event) {
        // Check for escape key
        switch (event.keyCode) {
            case 13: // Enter.
                //event.stopPropagation();
                //event.preventDefault();
                //this.resolve();
                break;
            default:
                super._evtKeydown(event);
                break;
        }
    }
}
export function showDialogEnter(options = {}) {
    let dialog = new DialogEnter(options);
    dialog.launch();
    // setTimeout(function(){console.log('go away'); dialog.resolve(0);}, 3000);
    return;
}
export function popup(b) {
    if (!(notImplemented.includes(b.req))) {
        popupTitle(b, b.popupTitle);
    }
    else {
        console.log("not implemented yet");
        popupResult("Not Implemented yet", "Not Implemented yet");
    }
}
export function popupTitle(b, popupTitle) {
    showDialogEnter({
        title: popupTitle,
        body: b,
        focusNodeSelector: 'input',
        buttons: [Dialog.okButton({ label: 'Ok' }), Dialog.cancelButton({ label: 'Cancel' })]
    });
}
export function popupResult(b, popupTitle) {
    showDialogEnter({
        title: popupTitle,
        body: b,
        focusNodeSelector: 'input',
        buttons: [Dialog.okButton({ label: 'Ok' })]
    });
}
export function isEmpty(obj) {
    return Object.keys(obj).length === 0;
}
